.container {
  height: auto;
  width: 450px;
}

.row {
  position: relative;
  display: block;
  width: 100%;
  height: 40px;
  border-bottom: 1px dashed #afafaf;
}

.name {
  position: relative;
  display: inline-block;
  line-height: 45px;
  color: #0792ff;
}

.score {
  position: absolute;
  display: inline-block;
  color: #071cff;
  right: 0;
  margin-top: 10px;
}

@media screen and (max-width: 500px) {
  .container {
    height: auto;
    width: 88vw;
    margin-bottom: 50px;
  }
}
