.Home {
  background-color: rgb(0, 0, 0);
  font-size: 1.2rem;
  padding: 3%;
}

.leaderboard-container {
  margin-top: -70px;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  color: grey;
}

h1 {
  background: -webkit-linear-gradient(rgb(28, 216, 210), rgb(0, 56, 210));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.logo-img {
  width: 200px;
  margin-left: 7%;
  margin-top: 2%;
}

button {
  background: #0792ff;
  border-radius: 5px;
  border: 0;
  padding: 0.5% 1% 0.5% 1%;
  color: aliceblue;
  width: 200px;
  height: 50px;
  font-size: larger;
  margin-top: 3%;
}

button:hover {
  background-color: blue;
}

.left {
  width: 400px;
  margin-right: 10%;
}

.play {
  margin-top: 10%;
}

@media screen and (max-width: 500px) {
  .logo-img {
    margin-left: 10%;
    margin-top: 10%;
  }
  .main {
    display: block;
  }
  .left {
    width: auto;
    margin-left: 10px;
  }

  .leaderboard-container {
    margin-top: auto;
    margin-left: 10px;
  }
}
